@font-face {
  font-family: 'CardenioModernBold';
  src: url('/static/ressources/fonts/Cardenio-Modern-Bold/CardenioModernBold.ttf');
}

// colors palette from: https://www.canva.com/colors/color-palettes/water-foam/
$background-image: url(static/ressources/background2.jpg);
$rose-1: #f9f1f0;
$rose-2: #FADCD9;
$rose-3: #F8AFA6;
$rose-4: #F79489;
$peach:  #FBE7C6;

* {
  padding: 0;
  margin: 0;
  font-family: CardenioModernBold,Asans-serif;
  color: white;
  text-shadow: 1px 1px 1px black, -1px -1px 10px black
}

.hidden {
  display: none;
}

.clickable {
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  &:hover {
    transform: scale(1.1);
  }
}

.centered {
  display: grid;
  place-items: center;
}
.padded {
  padding: 1rem;
}

.modal-container {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgb(0,0,0,0.4);
  .modal-window {
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.5) 0px 10px 20px;
    border-radius: 25px;
    background-color: $peach;
  }
}

.flex-container {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  &.column {
    flex-direction: column;
  }
  &.wrap {
    flex-wrap: wrap;
  }
  &.align-start {
    align-items: flex-start;
  }
  &.justify-start {
    justify-content: flex-start;
  }
}

body {
  height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

header {
  padding: 2rem;
  background-color: $rose-3;
}

footer {
  padding: 2rem;
  background-color: $rose-3;
}

.card {
  box-shadow: rgba(0, 0, 0, 0.5) 0px 10px 20px;
  background-color: $rose-2;
  padding: 2rem;
  border-radius: 1rem;
  .game-img {
    flex: 0 1 200px;
    width: 400px;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    margin: 1rem;
  }
}

.game-img {
  flex: 0 1 200px;
  width: 400px;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  margin: 1rem;
}

main {
  padding: 2rem 10rem;
  background-color: $rose-1;
}